@import url('https://fonts.googleapis.com/css?family=Fredericka+the+Great&display=swap');


.coding-text{
    font-size: 60px;
}

.bg-blue{
    height: 100vh;
    width: 100vw;
}

.bg-nav-bar{
    background: #000000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #474642, #140b0a);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2e2e2d, #0c0100); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.hero-section{
   font-family: 'Fredericka the Great', cursive;
   font-size:36px;
}
 

body {
  text-align:center;
  color:#fff;
  font-weight:100;
  overflow:hidden;
  animation:bg 5s linear infinite;
}

.experience {
  display: inline-block;
  width: 180px;
  text-align: left;
  height: 36px;
  vertical-align: -2px;
}

.experience > div {
  font-size:0px;
  opacity:0;

  position:absolute;
  font-weight:300;   
  box-shadow: 0px 60px 25px -20px rgba(0,0,0,0.5);
}

.experience > div:nth-child(1) {
  animation: roll 5s linear infinite 0s;
}
.experience > div:nth-child(2) {
  animation: roll 5s linear infinite 1s;
}
.experience > div:nth-child(3) {
  animation: roll 5s linear infinite 2s;
}
.experience > div:nth-child(4) {
  animation: roll 5s linear infinite 3s;
}
.experience > div:nth-child(5) {
  animation: roll2 5s linear infinite 3s;
}

@keyframes roll {
  0% {
    font-size:0px;
    opacity:0;
    margin-left:-30px;
    margin-top:0px;
    transform: rotate(-25deg);
  }
  3% {
    opacity:1;
    transform: rotate(0deg);
  }
  5% {
    font-size:inherit;
    opacity:1;
    margin-left:0px;
    margin-top:0px;
  }
  20% {
    font-size:inherit;
    opacity:1;
    margin-left:0px;
    margin-top:0px;
    transform: rotate(0deg);
  }
  27% {
    font-size:0px;
    opacity:0.5;
    margin-left:20px;
    margin-top:100px;
  }
  100% {
    font-size:0px;
    opacity:0;
    margin-left:-30px;
    margin-top:0px;
    transform: rotate(15deg);
  }
}

@keyframes roll2 {
  0% {
    font-size:0px;
    opacity:0;
    margin-left:-30px;
    margin-top:0px;
    transform: rotate(-25deg);
  }
  3% {
    opacity:1;
    transform: rotate(0deg);
  }
  5% {
    font-size:inherit;
    opacity:1;
    margin-left:0px;
    margin-top:0px;
  }
  30% {
    font-size:inherit;
    opacity:1;
    margin-left:0px;
    margin-top:0px;
    transform: rotate(0deg);
  }
  37% {
    font-size:1500px;
    opacity:0;
    margin-left:-1000px;
    margin-top:-800px;
  }
  100% {
    font-size:0px;
    opacity:0;
    margin-left:-30px;
    margin-top:0px;
    transform: rotate(15deg);
  }
}

@keyframes bg {
  0% {background: #ff0075;}
  3% {background: #0094ff;}
  20% {background: #0094ff;}
  23% {background: #b200ff;}
  40% {background: #b200ff;}
  43% {background: #8BC34A;}
  60% {background: #8BC34A;}
  63% {background: #F44336;}
  80% {background: #F44336;}
  83% {background: #F44336;}
  100% {background: #F44336;}
}